.some-page-wrapper {
  margin: 15px;
  width: 100%;
  background-color: #35605a;
}

.jss1{
  overflow: scroll !important;
  background-color: transparent !important;
}

.row {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
}

.column {
  display: flex;
  flex-direction: column;
  flex-basis: 100%;
  flex: 1;
}

.double-column {
  display: flex;
  flex-direction: column;
  flex-basis: 100%;
  flex: 2;
}

.error {
  /* background-color: #35605A; */
  word-wrap: break-word;
  border-radius: 15px;
  padding: 8px;
  margin: 8px;
}

label {
  padding-right: 2px;
}

.sidebar {
  padding-left: 10px;
}

.sidebar .input {
  padding-bottom: 10px;
}

.cards-flex {
  width: 100%;
  text-align: center;
  align-items: baseline;
}

.card {
  display: inline-block;
  margin: 6px;
  max-width: 180px;
  height: 250px;
}
.MuiCard-root-1 {
  overflow: scroll !important;
  background-color: transparent !important;
}

.content {
  position: relative !important;
  left: 20% !important;
  width: calc(80% - 10px) !important;
  padding-left: 10px;
}

@media (max-width: 1000px) {
  .content {
    position: relative !important;
    left: 0% !important;
    width: calc(100% - 10px) !important;
    padding-left: 10px;
  }
}

.words {
  color: #35605a;
  margin-bottom: 10px;
}

.words button{
  margin-left: 5px;
}

.topicResul {
  width: 100%;
  overflow-y: hidden;
  white-space: nowrap;
  padding-bottom: 20px;
  border-right: 20px;
}

.wordData {
  display: inline-block;
  padding-left: 10px;
  min-width: 200px;
}

.loading {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.sidebar {
  text-align: center;
}

.input {
  width: 100%;
  text-align: center;
  margin-top: 5px;
}

.form label {
  word-wrap: break-word;
}
.button {
  width: 90%;
  padding: 10px;
  border-radius: 10px;
  margin-top: 15px;
  margin-bottom: 10px;
}

.button:hover {
  background-color: #35605a;
  border-color: #35605a;
  color: white;
}

.form .input input {
  float: right;
  margin-right: 10px;
  width: calc(50% - 10px);
}

.form select {
  height: 30px;
  margin-top: 10px;
  margin-bottom: 5px;
}

.button:disabled {
  background-color: #d8e4ff;
  border-color: #d8e4ff;
  color: white;
  cursor: not-allowed;
}

input {
  border-radius: 5px;
  padding-left: 10px;
}

.deleteButton {
  background-color: white !important;
  border-radius: 10px !important;
  display: inline !important;
}

.deleteButton:hover {
  background-color: #35605a !important;
  border-color: #35605a !important;
  color: white !important;
}

/* width */
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: transparent !important; 
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #6B818C !important;
  border-radius: 35px !important; 
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #6B818C !important; 
}

::-webkit-scrollbar-corner{
  width: 0px;
  background: transparent !important; 

}
